import { MockElement } from '@stencil/core/mock-doc';

/**
 * Simulates the missing assignedElements() method from HTMLSlotElement in jsdom.\
 * Used in jest tests.
 */
export function simulateAssignedElements() {
  (MockElement.prototype as any).assignedElements = function () {
    const parentComponentName = this['s-hn'].toLowerCase();
    const hostElement = document.querySelector(parentComponentName);
    const assignedElements = hostElement.querySelectorAll(`[slot="${this.name ?? ''}"]`);
    return assignedElements;
  }
}
